.header{
background-color: #131921;
display: flex;
align-items: center;
position: sticky;
top: 0;
z-index: 100; 
}

.header__logo{
    width: 100px;
    object-fit: contain;
    margin-left: 20px;
    margin-right: 20px;
    margin-top:16px;
    margin-bottom: 4px;
    padding : 3px;
}

.header__search{
    display: flex;
    flex: 1;
}

.header__searchInput{
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
    border-radius: 5px 0px 0px 5px ;
}

.header__searchIcon{
    padding: 5px;
    height: 22px !important;
    background-color:#cd9042;
    border-radius: 0px 5px 5px 0px ;
}

.header_Right_nav{
    display: flex;
    justify-content: space-evenly;
}

.header__link{
    color: white;
    text-decoration: none;
    margin-left:3px;
}


.header__option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}

.header__optionLine1{
    font-size: 10px;
}

.header__optionLine2{
    font-size: 13px;
    font-weight: 800;
}

.header__optionBasket{
    display: flex;
    align-items: center;
}

.header__BasketCount{
    margin-left: 10px;
    margin-right: 10px ;
}

/* first value applies to top-left corner, second value applies to top-right corner, third value applies to bottom-right corner, and fourth value applies to bottom-left corner):  */
.highlight:hover{
    border-radius: 5px 5px 5px 5px;
    border : 1px white solid;   
}